.text_style {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.21;
  color: #242a37;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #ffffff;
  font-family: 'Abril Fatface', cursive;
  margin: 0;
  padding-top: 14px;
  padding-bottom: 13px;
  /* width: 320px; */
  margin-right: auto;
  margin-left: auto;
  box-shadow: 0px 2px 3px rgba(9, 30, 63, 0.1);
  padding-left: 22px;
  padding-right: 22px;
}

.header_l {
  composes: header;
  /* justify-content: center; */
  padding-left: 22px;
  padding-right: 22px;
}

.logo {
  text-align: center;
  color: #242a37;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.35;
  text-decoration: none;
}

.blok {
  display: flex;
  align-items: center;
  /* justify-content: flex-start; */
}

.button_mobile,
.btn_desktop {
  background-color: #f5f7fa;
  display: block;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  margin-right: 14px;
  margin-left: 14px;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #242a37;
}

.blok_user {
  display: none;
}

.button_exit {
  border: none;
  border-bottom: 1px solid #242a37;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  composes: text_style;
}

.nav {
  display: flex;
  align-items: center;
}

.nav .link + .link {
  margin-left: 5px;
}

.link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 33px;
  height: 33px;
  margin-right: 5px;
  background-color: #f5f7fa;
  border-radius: 50%;
}

.active_link {
  composes: link;
  background-color: transparent;
}

.line {
  display: block;
  width: 1px;
  height: 33px;
  background-color: #e0e5eb;
}

@media screen and (min-width: 480px) {
  /* .header {
    width: 480px;
  } */
  .header_l {
    /* width: 480px; */
    /* width: 320px; */
  }
}

@media screen and (min-width: 768px) {
  .header {
    /* width: 768px; */
    padding-top: 14px;
    padding-bottom: 13px;
    justify-content: space-between;
    /* padding-left: 32px;
      padding-right: 32px; */
  }
  /* .header_l {
      composes: header;
    } */
  .button_mobile {
    display: none;
  }
  .blok_user {
    display: flex;
    align-items: center;
    width: 500px;
    justify-content: center;
  }
  .button_exit {
    margin-left: 8px;
  }
  .user_name {
    composes: text_style;
    margin: 0;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    /* width: 1280px; */
    justify-content: space-between;
    padding-left: 22px;
    padding-right: 22px;
  }
  .header_l {
    composes: header;
  }
  .blok_user {
    width: 587px;
    justify-content: start;
  }
}

.modal {
  padding-bottom: 48px;
  padding-top: 48px;
  padding-left: 22px;
  padding-right: 22px;
}

.modal_text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.38;
  text-align: center;
  margin: 0 0 20px 0;
  color: #242a37;
}

.btn_modal {
  display: flex;
  justify-content: space-evenly;
}

.btn_modal button {
  display: block;
  justify-content: space-evenly;
  border-radius: 0%;
  background-color: transparent;
  width: 98px;
  height: 40px;
  color: #242a37;
  cursor: pointer;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;
}

.btn_modal button:hover,
.btn_modal button:focus,
.btn_info:hover,
.btn_info:focus {
  color: #ffffff;
  background-color: #ff6b08;
  border: none;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
}
